___CSS_LOADER_EXPORT___.push([module.id, `body, html { margin: 0; min-height: 100vh; }

body { 
  font-family: 'Mitr', sans-serif;

  background-color: #6E5C62;
  color: #ffffff;
}

.content { 
  padding: 1%;

  gap: 10px;

  display: grid;
  place-items: center;
}

.grid-wordle {
  display: grid;
  grid-template-rows: repeat(5, 70px);
  gap: 15px;

  margin-bottom: 2%;
}

.grid-wordle > .line {
  display: grid;
  grid-template-columns: repeat(5, 80px);
  gap: 10px;
}

.grid-wordle > .line > .letter {
  display: grid;
  place-items: center;
  height: 100%;

  border: 5px solid #4C4347;
  background-color: #615458;
  color: #ffffff;
  border-radius: 8px;

  font-size: 22px;
  font-weight: 700;
}

.keyboard {
  display: grid;
  place-items: center;
  height: 100%;
  margin-top: 20px;
}

.row {
  display: flex;
  justify-content: center;
  gap: 5px;
}

.row > button {
  font-family: 'Mitr', sans-serif;
  font-size: 20px;
  font-weight: 700;
}

.row > button:hover {
  background-color: #5e474e;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.key, .enter, .backspace {
  padding: 12px 25px;
  background-color: #4C4347;
  color: #ffffff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-family: 'Mitr', sans-serif;
  font-weight: 400;
  font-size: 18px;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 700,
  'GRAD' 0,
  'opsz' 48
}

.notification {
  display: none;
  font-family: 'Mitr', sans-serif;
  font-weight: 400;
  font-size: 20px;
}

.reset {
  display: none;
}

.reset > button {
  height: 50px;
  width: 150px;

  background-color: #3b3637;
  color: #ffffff;

  cursor: pointer;

  font-family: 'Mitr', sans-serif;
  font-weight: 700;

  border: none;
  border-radius: 5px;
}

.reset > button:hover {
  background-color: #464141;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.correct-letter {
  background-color: #3AA394 !important;
}

.correct-letter:hover {
  background-color: #27c9b0 !important;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.incorrect-position {
  background-color: #D3AD69 !important;
}

.incorrect-position:hover {
  background-color: #e4a02c !important;
}

.non-existent {
  background-Color: #594B4F;
  color: #6E5C62;
}`, "",{"version":3,"sources":["webpack://./src/views/style.css"],"names":[],"mappings":"AAAA,aAAa,SAAS,EAAE,iBAAiB,EAAE;;AAE3C;EACE,+BAA+B;;EAE/B,yBAAyB;EACzB,cAAc;AAChB;;AAEA;EACE,WAAW;;EAEX,SAAS;;EAET,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,mCAAmC;EACnC,SAAS;;EAET,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,sCAAsC;EACtC,SAAS;AACX;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,YAAY;;EAEZ,yBAAyB;EACzB,yBAAyB;EACzB,cAAc;EACd,kBAAkB;;EAElB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,QAAQ;AACV;;AAEA;EACE,+BAA+B;EAC/B,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,yBAAyB;EACzB,uDAAuD;AACzD;;AAEA;EACE,kBAAkB;EAClB,yBAAyB;EACzB,cAAc;EACd,YAAY;EACZ,mBAAmB;EACnB,eAAe;EACf,+BAA+B;EAC/B,gBAAgB;EAChB,eAAe;EACf,uDAAuD;AACzD;;AAEA;EACE;;;;;AAKF;;AAEA;EACE,aAAa;EACb,+BAA+B;EAC/B,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,YAAY;EACZ,YAAY;;EAEZ,yBAAyB;EACzB,cAAc;;EAEd,eAAe;;EAEf,+BAA+B;EAC/B,gBAAgB;;EAEhB,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,yBAAyB;EACzB,uDAAuD;AACzD;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;EACpC,uDAAuD;AACzD;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,yBAAyB;EACzB,cAAc;AAChB","sourcesContent":["body, html { margin: 0; min-height: 100vh; }\r\n\r\nbody { \r\n  font-family: 'Mitr', sans-serif;\r\n\r\n  background-color: #6E5C62;\r\n  color: #ffffff;\r\n}\r\n\r\n.content { \r\n  padding: 1%;\r\n\r\n  gap: 10px;\r\n\r\n  display: grid;\r\n  place-items: center;\r\n}\r\n\r\n.grid-wordle {\r\n  display: grid;\r\n  grid-template-rows: repeat(5, 70px);\r\n  gap: 15px;\r\n\r\n  margin-bottom: 2%;\r\n}\r\n\r\n.grid-wordle > .line {\r\n  display: grid;\r\n  grid-template-columns: repeat(5, 80px);\r\n  gap: 10px;\r\n}\r\n\r\n.grid-wordle > .line > .letter {\r\n  display: grid;\r\n  place-items: center;\r\n  height: 100%;\r\n\r\n  border: 5px solid #4C4347;\r\n  background-color: #615458;\r\n  color: #ffffff;\r\n  border-radius: 8px;\r\n\r\n  font-size: 22px;\r\n  font-weight: 700;\r\n}\r\n\r\n.keyboard {\r\n  disp